/*--------------------------------------------------------------
# Posts Layout 2
--------------------------------------------------------------*/

.aarambha-kits-block-posts-2-container .aarambha-kits-columns-wrapper.overlay-content .aarambha-kits-posts-wrapper {
    position: relative;
}

.aarambha-kits-block-posts-2-container .aarambha-kits-columns-wrapper.overlay-content .aarambha-kits-post-detail-wrap {
    position: absolute;
    z-index: 1;
    bottom: 0;
    right: 0;
    left: 0;
    padding: 10px;
}

.aarambha-kits-block-posts-2-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}

.aarambha-kits-block-posts-2-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap>*.element-order:not(:last-child) {
    padding-bottom: 10px;
}

.aarambha-kits-block-posts-2-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .entry-title {
    font-weight: 500;
}

.aarambha-kits-block-posts-2-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .entry-categories>* {
    background-color: #289dcc;
    padding: 5px 10px;
    color: #fff;
    font-size: 12px;
    text-transform: uppercase;
    display: inline-block;
    margin-top: 3px;
}

.aarambha-kits-block-posts-2-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .aarambha-kits-entry-meta .byline img {
    border-radius: 50%;
}

.aarambha-kits-block-posts-2-container .aarambha-kits-posts-wrapper {
    height: 100%;
}

.aarambha-kits-block-posts-2-container .aarambha-kits-posts-wrapper:not(.overlay-content .aarambha-kits-posts-wrapper) {
    background-color: #F3F3F3;
}

.aarambha-kits-block-posts-2-container .overlay-content .aarambha-kits-posts-wrapper .post-thumbnail-wrap {
    position: relative;
}

.aarambha-kits-block-posts-2-container .overlay-content .aarambha-kits-posts-wrapper .post-thumbnail-wrap::after {
    content: "";
    position: absolute;
    z-index: 1;
    left: 0;
    right: 0;
    bottom: 0;
    height: 65%;
    -webkit-transition: opacity 0.2s;
    -o-transition: opacity 0.2s;
    transition: opacity 0.2s;
    background-image: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0, 0, 0, 0.75)));
    background-image: -o-linear-gradient(top, transparent, rgba(0, 0, 0, 0.75));
    background-image: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.75));
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.aarambha-kits-block-posts-2-container .overlay-content.aarambha-kits-columns-wrapper .aarambha-kits-posts-wrapper {
    height: auto;
    border: none;
    margin-bottom: 0;
    overflow: hidden;
}

.aarambha-kits-block-posts-2-container .overlay-content .post-category,
.aarambha-kits-block-posts-2-container .overlay-content .post-category:hover,
.aarambha-kits-block-posts-2-container .overlay-content .entry-title a,
.aarambha-kits-block-posts-2-container .overlay-content .aarambha-kits-post-detail-wrap .aarambha-kits-entry-meta a {
    color: #fff;
}

.aarambha-kits-block-posts-2-container .overlay-content .entry-title a:hover,
.aarambha-kits-block-posts-2-container .overlay-content .aarambha-kits-post-detail-wrap .aarambha-kits-entry-meta a:hover {
    color: #289DCC;
}

.aarambha-kits-block-posts-2-container .overlay-content .aarambha-kits-entry-meta>div::before {
    background: #fff;
}

@media only screen and (max-width: 479px) {
    .aarambha-kits-block-posts-2-container .aarambha-kits-columns-wrapper.overlay-content .aarambha-kits-post-detail-wrap {
        background: rgba(0, 0, 0, 0.5);
        position: static;
    }
}